(function(){
    'use strict';
    // formatBytes Taken from: https://stackoverflow.com/questions/15900485/correct-way-to-convert-size-in-bytes-to-kb-mb-gb-in-javascript
    Number.prototype.formatBytes = function() {
        var units = ['B', 'KB', 'MB', 'GB', 'TB'],
            bytes = this,
            i;
     
        for (i = 0; bytes >= 1024 && i < 4; i++) {
            bytes /= 1024;
        }
     
        return bytes.toFixed(2) + units[i];
    }

    // The Office initialize function must be run each time a new page is loaded.
    Office.initialize = function(reason){
        $(document).ready(function(){
            var sender = JSON.parse(localStorage.getItem("sender"));
            var subject = localStorage.getItem("subject");
            var attachments = JSON.parse(localStorage.getItem("attachments"));
            //TODO: check if values were actually retrieved from storage
            
            $("#sender-id").text(sender.emailAddress);
            $("#subject-id").text(subject); 
            if(attachments.length > 0){
                console.log("SOI-LOG: attachments found");
                $("#attachments-container").css("display", "inline");
                for(let i = 0; i < attachments.length; i++){
                    var attachment = attachments[i];
                    $("#attachments-id").append("<li>" +attachment.name + ", "+ attachment.size.formatBytes() +"</li>")
                }
            }
            $("#info-container").css("display", "inline");
        })
    };
  })();

